.Select {
    margin-bottom: 15px;
}

.Select label {
    margin: 0 0 3px 0;
    padding: 0;
    font-weight: bold;
    display: block;
}

.Select select {
    font-size: 18px;
    display: block;
    box-sizing: border-box;
    /*border: 1px solid #bebebe;*/
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, .1);
