@import "../../../variables";

.ButtonLogIn {
  outline: none;
  background: transparent;
  color: #fff;
  display: flex;
  align-items: center;
  max-height: 41px;
  cursor: pointer;

  border-radius: 12px;
  padding: 10px 15px;

  &:hover {
    border: 1px solid $secondary-color;
    transform: translateX(1px);
  }

  &:active {
    transform: scale(.95);
    border: 1px solid $secondary-color;
  }

  &__img {
    margin-right: 7px;
    object-fit: contain;
    max-height: 25px;
  }

  &__text {
    font-size: 1rem;
    border-bottom: 2px dotted #fff;
  }
}