/* полоса прокрутки (скроллбар) */
::-webkit-scrollbar {
    width: 10px; /* ширина для вертикального скролла */
    height: 8px; /* высота для горизонтального скролла */
    background-color: #fff;
}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
    background-color: rgba(0,0,0,0.3);
    border-radius: 9em;
    box-shadow: inset 1px 1px 10px #f3faf7;
}

::-webkit-scrollbar-thumb:hover {
    background-color: rgba(0,0,0,0.8);
}

/* Стрелки */

::-webkit-scrollbar-button:vertical:start:decrement {
    background: linear-gradient(120deg, #999 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(240deg, #999 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(0deg, #999 30%, rgba(0, 0, 0, 0) 31%);
    background-color: #fff;
}

::-webkit-scrollbar-button:vertical:end:increment {
    background: linear-gradient(300deg, #999 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(60deg, #999 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(180deg, #999 30%, rgba(0, 0, 0, 0) 31%);
    background-color: #fff;
}

::-webkit-scrollbar-button:horizontal:start:decrement {
    background: linear-gradient(30deg, #999 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(150deg, #999 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(270deg, #999 30%, rgba(0, 0, 0, 0) 31%);
    background-color: #fff;
}

::-webkit-scrollbar-button:horizontal:end:increment {
    background: linear-gradient(210deg, #999 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(330deg, #999 40%, rgba(0, 0, 0, 0) 41%),
    linear-gradient(90deg, #999 30%, rgba(0, 0, 0, 0) 31%);
    background-color: #fff;
}