@import "../../variables";

.RetailCheckPanel {
  //max-width: 860px;
  padding: 0;

  &:not(:last-child) {
    border-bottom: 1px solid black;
  }

  &__mainContainer {
    display: flex;
    justify-content: space-between;
    padding: 5px 20px 5px 25px;
  }

  &__address {
    cursor: pointer;

    &:hover {
      color: blue;
      border-bottom: 1px dashed blue;
    }
  }

  &__btnContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 0 0 20px;
  }

  &__button {
    max-width: 180px;
    text-align: center;
    width: 100%;
    font-size: .95rem;
    background: #fff;
    color: $primary-color;
    cursor: pointer;
    outline: none;
    padding: 10px 18px;
    border: 1px solid $primary-color;
    border-radius: 18px;
    justify-content: center;
    white-space: nowrap;

    transition: background-color .2s, color .2s;

    &:hover {
      background: $primary-color;
      color: #fff;
    }
  }

  &__content {
    color: #3e3e3e;
    margin: 5px 0;
    font-weight: 700;
    flex: 1 1;

    p {
      margin: 2px 0;
      font-weight: 600;

      span {
        color: #000;
        font-weight: normal;
        margin-left: 5px;
        font-size: 16px;
      }

      img {
        width: 15px;
        height: 15px;
        transform: translate(-10px, 2px);
      }
    }
  }

  &__block {
    margin-right: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__priceContainer {
    padding: 0 20px;
    min-width: 180px;
    max-width: 380px;
    width: 100%;
    display: flex;
    align-items: center;
    @media (max-width: 1150px) {
      flex: 0 1;
    }
  }

  &__price {
    max-width: 320px;
    width: 100%;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    text-align: right;

    @media (max-width: 1150px) {
      flex-direction: column;
    }

    p {
      font-weight: bold;
      font-size: 18px;
      line-height: 1.4;
      color: #333;
      white-space: nowrap;
    }
  }
}

